@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/roboto-condensed-300.woff2") format("woff2"), url("../fonts/roboto-condensed-300.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2"), url("../fonts/roboto-condensed-400.woff") format("woff");
}

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
}

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/bebas-neue-400.woff2") format("woff2"), url("../fonts/bebas-neue-400.woff") format("woff");
}

@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/bebas-neue-700.woff2") format("woff2"), url("../fonts/bebas-neue-700.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  color: #102654;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 103px;
}

@media (max-width: 1199px) {
  .container {
    max-width: 1199px;
    padding: 0 45px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 767px;
    padding: 0 15px;
  }
}

h1 {
  width: 100%;
  margin: 0;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  font-size: 109px;
  line-height: 1;
  letter-spacing: 0.03em;
  color: #ffffff;
  text-align: right;
  -webkit-text-stroke: 2px #ffffff;
}

h1 span {
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 400;
  font-size: 49px;
  letter-spacing: 0.02em;
  -webkit-text-stroke-width: 0;
}

@media (max-width: 1199px) {
  h1 {
    width: 310px;
    font-size: 86px;
    -webkit-text-stroke-width: 4px;
  }
  h1 span {
    font-size: 34px;
    letter-spacing: 0.05em;
  }
}

@media (max-width: 767px) {
  h1 {
    width: 290px;
    padding-left: 20px;
    font-size: 78px;
  }
  h1 span {
    font-size: 28px;
    letter-spacing: 0.03em;
  }
}

.site-logo {
  width: 89px;
  height: 20px;
}

@media (max-width: 1199px) {
  .site-logo {
    width: 82px;
    height: 17px;
  }
}

@media (max-width: 767px) {
  .site-logo {
    width: 68px;
    height: 15px;
  }
}

.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 1;
  letter-spacing: 0.03em;
  text-align: right;
  word-break: break-all;
}

.contacts li:not(:last-child) {
  margin-bottom: 8px;
}

.contacts a {
  display: inline-block;
  color: #ffffff;
  vertical-align: top;
}

.contacts a:hover, .contacts a:focus {
  color: rgba(255, 255, 255, 0.6);
}

.contacts a:focus {
  outline: 1px dotted rgba(255, 255, 255, 0.8);
  outline-offset: 2px;
}

.contacts a:active {
  color: rgba(255, 255, 255, 0.3);
  outline: 0;
}

@media (max-width: 1199px) {
  .contacts {
    font-size: 24px;
    letter-spacing: 0.02em;
  }
}

@media (max-width: 767px) {
  .contacts {
    font-size: 20px;
  }
}

.button {
  display: inline-block;
  width: 206px;
  padding: 24px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: #102654;
  text-align: center;
  vertical-align: middle;
  text-transform: uppercase;
  word-break: break-all;
  border: 2px solid #102654;
}

.button:hover, .button:focus {
  padding: 26px;
  color: #ffffff;
  background-image: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  border: none;
}

.button:focus {
  outline: 1px dotted #102654;
  outline-offset: 2px;
}

.button:active {
  opacity: 0.2;
  outline: none;
}

.button--light {
  width: 230px;
  padding: 19px;
  color: #ffffff;
  border: 4px solid #ffffff;
}

.button--light:hover, .button--light:focus {
  padding: 19px;
  color: #102654;
  background-color: #ffffff;
  background-image: none;
  border: 4px solid #ffffff;
}

.button--light:focus {
  outline: 1px dotted rgba(255, 255, 255, 0.8);
  outline-offset: 2px;
}

.button--light:active {
  opacity: 0.2;
  outline: none;
}

li:hover .button {
  padding: 26px;
  color: #ffffff;
  background-image: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  border: none;
}

@media (max-width: 1199px) {
  .button--light {
    width: 187px;
  }
}

@media (max-width: 767px) {
  .button--light {
    width: 100%;
  }
}

.socials {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 1.2;
  text-align: right;
}

.socials li {
  margin-bottom: 18px;
  margin-left: 18px;
}

.socials a {
  color: #ffffff;
}

.socials a:hover, .socials a:focus {
  color: rgba(255, 255, 255, 0.6);
}

.socials a:focus {
  outline: 1px dotted rgba(255, 255, 255, 0.8);
  outline-offset: 2px;
}

.socials a:active {
  color: rgba(255, 255, 255, 0.3);
  outline: 0;
}

@media (max-width: 1199px) {
  .socials {
    display: none;
  }
}

.promo {
  display: flex;
}

.promo__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-content: flex-start;
  align-items: flex-start;
  width: 395px;
  margin-top: 66px;
  margin-right: 65px;
}

.promo__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 113px;
  margin-top: -93px;
  margin-right: 33px;
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
  text-align: right;
}

.promo__button {
  margin-top: 55px;
}

.promo__photo img {
  vertical-align: top;
}

.promo__socials {
  align-self: flex-end;
  margin-left: auto;
  margin-top: 115px;
  margin-bottom: 30px;
}

@media (max-width: 1199px) {
  .promo {
    justify-content: center;
  }
  .promo__header {
    width: 330px;
    margin-top: 10px;
    margin-right: 4%;
  }
  .promo__list {
    margin-top: -82px;
    margin-right: 26px;
  }
  .promo__button {
    margin-top: 39px;
  }
  .promo__photo {
    max-width: 40%;
    min-width: 324px;
    max-height: 503px;
    min-height: 390px;
    overflow: hidden;
  }
  .promo__photo img {
    max-width: 102%;
    height: 102%;
    object-fit: cover;
    object-position: -18px 0;
  }
}

@media (max-width: 767px) {
  .promo__header {
    justify-content: flex-start;
    width: 52%;
    min-width: 290px;
    margin-top: 0;
    margin-right: 0;
  }
  .promo__header h1 {
    margin-left: auto;
  }
  .promo__list {
    margin-top: -75px;
    margin-right: 100px;
  }
  .promo__photo {
    display: none;
  }
  .promo__button {
    margin-top: 44px;
  }
}

.page-header {
  margin-bottom: 120px;
  padding-top: 30px;
  padding-bottom: 46px;
  background-color: #d10a42;
  background-image: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
}

.page-header__top-wrapper {
  display: grid;
  grid-template-columns: 89px 1fr;
  column-gap: 40px;
}

.page-header__contacts {
  justify-self: end;
}

@media (max-width: 1199px) {
  .page-header {
    margin-bottom: 99px;
    padding-top: 39px;
    padding-bottom: 82px;
    background-image: conic-gradient(from 139.4deg at 20.7% 35.74%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
  .page-header__top-wrapper {
    margin-bottom: 19px;
  }
}

@media (max-width: 767px) {
  .page-header {
    margin-bottom: 69px;
    padding-top: 19px;
    padding-bottom: 79px;
    background-image: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  }
  .page-header__top-wrapper {
    max-width: 680px;
    margin-bottom: 35px;
  }
}

.gym {
  display: flex;
  justify-content: flex-end;
  max-width: 1366px;
  padding: 0 103px;
}

.gym__left-column {
  width: 355px;
  margin-right: 22px;
}

.gym__left-column p {
  margin: 0;
  margin-bottom: 9px;
  font-weight: 300;
  line-height: 1.5;
}

.gym__header {
  margin-bottom: 38px;
}

.gym__header h2 {
  margin-top: 46px;
  margin-bottom: 9px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.03em;
  color: #102654;
  font-size: 49px;
}

.gym__header p {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
}

.gym__right-column {
  width: 62.4%;
}

.gym__photo {
  max-width: 660px;
  max-height: 401px;
  margin-left: 63px;
  overflow: hidden;
}

.gym__photo img {
  vertical-align: top;
  object-position: 0 -37px;
}

.gym__video {
  position: relative;
  width: 364px;
  height: 228px;
  margin-top: -113px;
  cursor: pointer;
}

.gym__video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gym__video-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.gym__video-link--hidden {
  display: none;
}

.gym__video-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/bg-gym-video.jpg");
  background-repeat: no-repeat;
  background-position: -2px -27px;
  background-size: 394px auto;
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .gym__video-link::before {
    background-image: url("../img/bg-gym-video@2x.jpg");
  }
}

.gym__video-link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 54, 122, 0.5);
  background-image: url("../img/bg-decor-video.svg");
  background-repeat: no-repeat;
  background-position: 50%;
}

.gym__video-link:hover::after, .gym__video-link:focus::after {
  background-color: transparent;
  box-shadow: 0 0 10px 0 rgba(3, 10, 26, 0.75);
}

.gym__video-link:focus {
  outline: 1px dotted #102654;
  outline-offset: 7px;
}

.gym__video-link:active::after {
  opacity: 0.7;
}

@media (max-width: 1199px) {
  .gym {
    flex-direction: column;
    align-items: center;
    max-width: 1199px;
    padding: 0 45px;
    text-align: center;
  }
  .gym__left-column {
    width: 80%;
    margin-right: 0;
  }
  .gym__left-column p {
    margin-bottom: 44px;
  }
  .gym__left-column p:not(:first-of-type) {
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden;
  }
  .gym__header {
    margin-bottom: 33px;
  }
  .gym__header h2 {
    margin-top: 0;
    font-family: "Bebas Neue", "Arial", sans-serif;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.03em;
    color: #102654;
    font-size: 42px;
  }
  .gym__header p {
    margin: 0;
  }
  .gym__right-column {
    width: 100%;
  }
  .gym__photo {
    max-width: 678px;
    max-height: 345px;
    margin: 0 auto;
    overflow: hidden;
  }
  .gym__photo img {
    max-width: 114%;
    height: 399px;
    object-position: -89px -18px;
  }
  .gym__video {
    width: 266px;
    height: 167px;
    margin: 0 auto;
    margin-top: -84px;
  }
  .gym__video-link::before {
    background-position: -2px -20px;
    background-size: 287px auto;
  }
  .gym__video-link::after {
    background-size: 73px 73px;
  }
}

@media (max-width: 767px) {
  .gym {
    max-width: 100%;
    padding: 0;
    text-align: left;
  }
  .gym__left-column {
    width: 100%;
    padding: 0 15px;
  }
  .gym__left-column p {
    max-width: 680px;
    margin: 0 auto;
    margin-bottom: 33px;
    padding-right: 27px;
  }
  .gym__header {
    max-width: 680px;
    margin: 0 auto;
    margin-bottom: 24px;
  }
  .gym__header h2 {
    margin-bottom: 6px;
    font-family: "Bebas Neue", "Arial", sans-serif;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.03em;
    color: #102654;
    font-size: 32px;
  }
  .gym__header p {
    margin-bottom: 0;
    padding-right: 0;
  }
  .gym__photo {
    width: 100%;
    height: 313px;
    margin-bottom: 40px;
  }
  .gym__photo img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 90% 50%;
  }
  .gym__video {
    width: 100%;
    max-width: 678px;
    margin-top: 0;
  }
  .gym__video-link {
    background-image: linear-gradient(rgba(20, 54, 122, 0.5), rgba(20, 54, 122, 0.5)), url("../img/bg-gym-video.jpg");
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 767px auto;
  }
}

@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .gym__video-link {
    background-image: linear-gradient(rgba(20, 54, 122, 0.5), rgba(20, 54, 122, 0.5)), url("../img/bg-gym-video@2x.jpg");
  }
}

@media (max-width: 767px) {
  .gym__video-link::before {
    background-position: 50% 5%;
    background-size: 320px 127%;
  }
}

.season-tickets {
  text-align: center;
  background-color: #f8f9fc;
  background-image: url("../img/bg-season-tickets.png"), linear-gradient(0deg, #ffffff 71px, #f8f9fc 71px);
  background-repeat: no-repeat;
  background-position: 100% 100%, 0 0;
}

.season-tickets h2 {
  margin: 0;
  margin-bottom: 35px;
  font-family: "Bebas Neue", "Arial", sans-serif;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.03em;
  color: #102654;
  font-size: 49px;
  text-align: center;
}

.season-tickets__tabs {
  padding-top: 118px;
  padding-bottom: 149px;
}

.season-tickets__tab-switchers {
  position: relative;
  display: none;
  margin-bottom: 39px;
  font-size: 0;
  vertical-align: top;
}

.season-tickets__tab-switchers--shown {
  display: inline-block;
}

.season-tickets__tab-switchers::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(16, 38, 84, 0.2);
}

.season-tickets__tab-switcher {
  position: relative;
  margin-bottom: 13px;
  padding: 0;
  font-family: inherit;
  font-size: 20px;
  line-height: 1.5;
  color: rgba(16, 38, 84, 0.5);
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.season-tickets__tab-switcher:not(:last-child) {
  margin-right: 46px;
}

.season-tickets__tab-switcher--active {
  color: inherit;
  cursor: default;
}

.season-tickets__tab-switcher--active::before {
  content: "";
  position: absolute;
  bottom: -13px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #d10a42;
}

.season-tickets__tab-switcher:not(.season-tickets__tab-switcher--active):hover, .season-tickets__tab-switcher:not(.season-tickets__tab-switcher--active):focus {
  color: rgba(16, 38, 84, 0.7);
}

.season-tickets__tab-switcher:not(.season-tickets__tab-switcher--active):focus {
  outline: 1px dotted #102654;
}

.season-tickets__tab-switcher:not(.season-tickets__tab-switcher--active):active {
  color: rgba(16, 38, 84, 0.3);
  outline: none;
}

.season-tickets__offers {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.season-tickets__offers--hidden {
  display: none;
}

.season-tickets__offers li {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 31%;
  margin-bottom: 40px;
  padding: 0 40px;
  padding-top: 55px;
  padding-bottom: 61px;
  background-color: #ffffff;
  border: 7px solid #ffffff;
}

.season-tickets__offers li:not(:nth-child(3n)) {
  margin-right: 3.5%;
}

.season-tickets__offers li:hover {
  border: 7px solid transparent;
  border-image: linear-gradient(-225deg, #d10a42, #00359f);
  border-image-slice: 1;
}

.season-tickets__offers h3 {
  width: 80%;
  margin: 0;
  margin-bottom: 11px;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.1;
}

.season-tickets__offers p {
  margin: 0;
  margin-bottom: 38px;
}

.season-tickets__offers .season-tickets__ticket-price {
  margin-bottom: 52px;
}

.season-tickets__ticket-price {
  position: relative;
  width: 266px;
  font-size: 100px;
  font-weight: 700;
  line-height: 1;
  color: rgba(232, 232, 232, 0.47);
  word-break: break-all;
}

.season-tickets__ticket-price::before {
  content: attr(data-ticket-price);
  position: absolute;
  top: 43px;
  left: 50%;
  min-width: 166px;
  max-width: 300px;
  height: auto;
  padding-right: 45px;
  font-size: 60px;
  color: #103aae;
  text-align: left;
  word-wrap: break-word;
  word-break: normal;
  background-image: url("../img/bg-icon-ruble.svg");
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 30px 42px;
  transform: translateX(-50%);
}

.season-tickets__button {
  margin-top: auto;
}

@media (max-width: 1199px) {
  .season-tickets {
    background-image: url("../img/bg-season-tickets-tablet.png"), linear-gradient(0deg, #ffffff 53px, #f8f9fc 53px);
    background-repeat: no-repeat;
    background-position: 100% 100%, 0 0;
  }
  .season-tickets h2 {
    margin-bottom: 31px;
    font-family: "Bebas Neue", "Arial", sans-serif;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 0.03em;
    color: #102654;
    font-size: 42px;
  }
  .season-tickets__tabs {
    padding-top: 98px;
    padding-bottom: 111px;
  }
  .season-tickets__tab-switchers {
    margin-bottom: 44px;
  }
  .season-tickets__tab-switcher {
    font-size: 18px;
  }
  .season-tickets__tab-switcher:not(:last-child) {
    margin-right: 55px;
  }
  .season-tickets__offers li {
    width: 65.2%;
    margin-right: auto;
    margin-left: auto;
  }
  .season-tickets__offers li:not(:nth-child(3n)) {
    margin-right: auto;
    margin-left: auto;
  }
}

@media (max-width: 767px) {
